body {
	font-family: 'Raleway', sans-serif; }

.navbar-brand {
			position: relative;
			.bolt {
				position: absolute;
				left: 10px;
				bottom: 10px; } }


#work {
	background: url('/img/optional.png')no-repeat center center /cover; }

#latestNews {
	background: url('/img/optional2.png')no-repeat center center /cover; }

#clients {
	background-color: #F5F5F5; }

#product {
	background-color: #F5F5F5; }

#footer {
	background-color: #000000; }

@media screen and (max-width: 1100px) {
	.hide {
		display: none; }

	#work {
		button {
			display: block;
			margin: 0 auto; } }

	#sample {
		.container {
			margin-left: 70px; }
		.btn {
			display: block;
			margin-right: 0 auto; } } }

@media screen and (max-width: 768px) {
	.hide-sm {
		display: none; }
	#sample {
		.container {
			margin: 0 auto;
			h4 {
			  padding: 10px 110px; }
			.btn {
			  margin: 0 auto; }
			.scenter {
			  margin-left: 110px; } } }
	#product {
		.prcenter {
			margin-left: 50px; } } }
